* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Manrope, sans-serif;
}

body {
  background-image: url("bg-pattern.5651018f.svg");
  background-repeat: no-repeat;
  background-size: 100% 50%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 2rem 0 4rem;
  display: flex;
}

@media (width >= 1440px) {
  body {
    background-size: 100% 60%;
    gap: 1rem;
    height: 100vh;
    padding-top: 0;
  }
}

@keyframes swipeIn {
  0% {
    opacity: 0;
    transform: translateX(-300px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes showUp {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

header {
  background-image: url("pattern-circles.6217120f.svg");
  background-position: center;
  background-repeat: no-repeat;
  padding: 3rem 0;
  animation: .8s ease-in-out showUp;
}

header h1 {
  color: #293356;
  font-size: 1.3rem;
  line-height: 2;
}

header p {
  color: #858fad;
  text-align: center;
  font-size: 15px;
  line-height: 1.5;
}

main {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  width: 85%;
  max-width: 500px;
  animation: .6s ease-in swipeIn;
  box-shadow: 0 10px 10px 5px #eaeefb;
}

main .pageviews {
  margin: 2rem 1rem;
}

main .pageviews > p {
  color: #858fad;
  letter-spacing: 2px;
  font-size: 10px;
  font-weight: 800;
}

main .pageviews input[type="range"] {
  appearance: none;
  background: linear-gradient(to right, #a5f3eb 50%, #eaeefb 50%);
  border-radius: 10px;
  outline: none;
  width: 100%;
  height: 10px;
  margin: 2rem 0;
}

main .pageviews input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  cursor: pointer;
  background-color: #10d5c2;
  background-image: url("icon-slider.b7adef45.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20px;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  box-shadow: 0 15px 15px #a5f3eb;
}

main .pageviews input[type="range"]::-webkit-slider-thumb:hover {
  background-color: #12edd7;
}

main .pageviews input[type="range"]::-webkit-slider-thumb:active {
  background-color: #0ca697;
}

main .pageviews input[type="range"]::-moz-range-thumb {
  appearance: none;
  cursor: pointer;
  background-color: #10d5c2;
  background-image: url("icon-slider.b7adef45.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20px;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  box-shadow: 0 15px 15px #a5f3eb;
}

main .pageviews input[type="range"]::-moz-range-thumb:hover {
  background-color: #12edd7;
}

main .pageviews input[type="range"]::-moz-range-thumb:active {
  background-color: #0ca697;
}

main .pageviews h2 {
  color: #858fad;
  margin: 0 0 2rem;
  font-size: 15px;
  font-weight: 600;
}

main .pageviews h2 span {
  color: #000;
  font-size: 1.5rem;
  font-weight: 800;
}

main .pageviews .billing {
  color: #858fad;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  display: flex;
}

main .pageviews .billing p {
  font-size: 10px;
}

main .pageviews .billing .switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

main .pageviews .billing .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

main .pageviews .billing .switch input:checked + .toggle {
  background-color: #10d5c2;
}

main .pageviews .billing .switch input:checked + .toggle:before {
  transform: translateX(18px);
}

main .pageviews .billing .switch input:focus + .toggle {
  box-shadow: 0 0 1px #2196f3;
}

main .pageviews .billing .switch .toggle {
  cursor: pointer;
  background-color: #cdd7ee;
  border-radius: 50px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

main .pageviews .billing .switch .toggle:hover {
  background-color: #10d5c2;
}

main .pageviews .billing .switch .toggle:before {
  content: "";
  background-color: #fff;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  transition: all .4s;
  position: absolute;
  bottom: 3px;
  left: 4px;
}

main .pageviews .billing .discount {
  position: relative;
}

main .pageviews .billing .discount:after {
  content: "-25%";
  color: #ff8c66;
  cursor: text;
  background-color: #feece7;
  border-radius: 10px;
  margin-left: .3rem;
  padding: .1rem .4rem;
  font-size: 10px;
  position: absolute;
}

main .trial {
  border-top: 2px solid #eaeefb;
  padding-top: 1rem;
}

main .trial p {
  color: #858fad;
  font-size: 15px;
  line-height: 2;
  position: relative;
}

main .trial p:before {
  content: url("icon-check.e1420a70.svg");
  margin-right: 1rem;
}

main .trial button {
  color: #bdccff;
  cursor: pointer;
  background-color: #293356;
  border: none;
  border-radius: 50px;
  margin: 1.5rem 0;
  padding: .9rem 3rem;
  font-size: 15px;
}

main .trial button:hover {
  color: #fff;
}

@media (width >= 1440px) {
  header {
    text-align: center;
    margin-bottom: 1rem;
  }

  header h1 {
    font-size: 1.9rem;
  }

  header p {
    font-size: 1rem;
    display: inline-block;
  }

  main {
    width: 38%;
    max-width: 600px;
  }

  main .pageviews {
    grid-template-rows: repeat(3, 1fr);
    place-items: center;
    margin-bottom: 0;
    padding-bottom: 1rem;
    display: grid;
  }

  main .pageviews > p {
    grid-area: 1 / 1 / 2 / 2;
    font-size: 1rem;
  }

  main .pageviews h2 {
    grid-area: 1 / 2 / 2 / 3;
    margin: 0;
    font-size: 1.2rem;
    position: relative;
    top: -12px;
  }

  main .pageviews h2 span {
    font-size: 2.8rem;
    position: relative;
    top: 12px;
  }

  main .pageviews input[type="range"] {
    grid-column: span 2;
    width: 90%;
  }

  main .pageviews input[type="range"]::-webkit-slider-thumb {
    background-size: 25px;
    width: 40px;
    height: 40px;
  }

  main .pageviews input[type="range"]::-moz-range-thumb {
    background-size: 25px;
    width: 40px;
    height: 40px;
  }

  main .pageviews .billing {
    grid-column: span 2;
    gap: 2rem;
  }

  main .pageviews .billing p {
    font-size: 1rem;
  }

  main .pageviews .billing .switch {
    width: 55px;
    height: 25px;
  }

  main .pageviews .billing .switch input:checked + .toggle:before {
    transform: translateX(28px);
  }

  main .pageviews .billing .switch .toggle:before {
    width: 18px;
    height: 18px;
  }

  main .pageviews .billing .discount:after {
    content: "25% discount";
    width: max-content;
    font-size: 12px;
    top: 2px;
  }

  main .trial {
    grid-template-columns: 1fr 1fr;
    place-items: center;
    gap: 2rem;
    padding-bottom: 1.5rem;
    display: grid;
  }

  main .trial p {
    text-align: left;
  }
}

